import React from 'react';
import { useParams } from 'react-router-dom';
import './AboutShelter.css';
import shelterLogo from '../../images/shelter-logo.jpg';

const AboutShelter = () => {
  const { id } = useParams(); // id 
  const [cardInfo, setCardInfo] = React.useState({}); //    

  React.useEffect(() => {
    setCardInfo({ //    
      id: id,
      logo: '../../images/shelter-logo.jpg',
      title: ' ',
      address: ' -, 10, , ',
      workingHours: '10:00 - 18:00',
      phone: '+74955143389',
      email: 'email@email.com',
      link: 'https://izpriuta.ru',
      descriprion: `  -            . .
          2500   150 .   ,   ,    -      
       -      .`,
      vkLink: 'vk.com',
      telegramLink: 't.me',
      gettingAllMoney: 6457362,
      takingAllPets: 365,
    });
  }, [id]);

  return (
    <section className='shelter-section about-shelter'>
      <div className='shelter-info'>
        <img className='shelter-info__logo' src={shelterLogo} alt='' />
        <div className='shelter-info__text-container'>
          <h2 className='shelter-section__title'>{cardInfo.title}</h2>
          <ul className='shelter-info__list'>
            <li className='shelter-info__text'>: <span className='color_text_additional'>{cardInfo.address}</span></li>
            <li className='shelter-info__text'>-: <span className='color_text_additional'>{cardInfo.workingHours}</span></li>
            <li className='shelter-info__text'> : <span className='color_text_additional'>{cardInfo.phone}</span></li>
            <li className='shelter-info__text'>E-mail: <span className='color_text_additional'>{cardInfo.email}</span></li>
            <li className='shelter-info__text'>: <a className='shelter-info__link' href={cardInfo.link} target='_blank' rel="noreferrer">
              {cardInfo.link}
              </a></li>
          </ul>
          <div className='shelter-info__icons'>
            <a className='shelter-info__icon shelter-info__icon_image_vk' href={cardInfo.vkLink} target='_blank' rel="noreferrer" />
            <a className='shelter-info__icon shelter-info__icon_image_telegram' href={cardInfo.telegramLink} target='_blank' rel="noreferrer" />
          </div>
        </div>
      </div>
      <h2 className='shelter-section__title'></h2>
      <p className='about-shelter__description'>{cardInfo.descriprion}</p>
      <p className='about-shelter__text'>    : <span className='color_text_additional'>{cardInfo.gettingAllMoney}</span></p>
      <p className='about-shelter__text'>    : <span className='color_text_additional'>{cardInfo.takingAllPets}</span></p>
    </section>
  );
}

export default AboutShelter;
