import React from 'react';
import './NewPage.css';
import dataIcon from '../../images/icons/ic_data_dark.svg';
import selfIcon from '../../images/icons/ic_self_dark.svg';
import newPagePhoto from '../../images/new-page_photo.jpg';

const NewPage = () => {
  return (
    <main className='main new-page'>
      <h2 className='new-page__title'>    </h2>
      <div className='new-page__row'>
        <div className='new-page__info-block'>
          <img className='new-page__icon' src={dataIcon} alt='' />
          <p className='new-page__info'>12.12.2022</p>
        </div>
        <div className='new-page__info-block'>
          <img className='new-page__icon' src={selfIcon} alt='' />
          <p className='new-page__info'> </p>
        </div>
      </div>
      <div className='new-page__photos'>
        <img className='new-page__main-photo' src={newPagePhoto} alt=' ' />
        <div className='new-page__photo-column'>
          <img className='new-page__additional-photo' src={newPagePhoto} alt='' />
          <img className='new-page__additional-photo' src={newPagePhoto} alt='' />
          <img className='new-page__additional-photo' src={newPagePhoto} alt='' />
        </div>
      </div>
      <p className='new-page__text'>    4 ""    2010     ! 
                    .</p>
      <p className='new-page__text'>      .     !</p>
    </main>
  );
}

export default NewPage;
