import React from 'react';
import { useNavigate } from "react-router-dom";
import './Banner.css';
import bannerImage from '../../images/main__banner.svg';

const Banner = () => {
  const navigate = useNavigate(); 

  return (
    <section className='banner'>
      <img className='banner__image' src={bannerImage} alt='' />
      <h1 className='banner__title'> ,    </h1>
      <div className='banner__line'></div>
      <p className='banner__subtitle'>  -      .</p>
      <button className='banner__button button' type='button' onClick={() => navigate('/shelters')}> </button>
    </section>
  );
}

export default Banner;
