import React from 'react';
import { Link } from 'react-router-dom';
import './Footer.css';
import classmatesIcon from '../../images/icons/footer__icon_classmates.svg';
import vkIcon from '../../images/icons/footer__icon_vk.svg';
import telegramIcon from '../../images/icons/footer__icon_telegram.svg';

const Footer = () => {
  return (
    <footer className='footer'>
      <div className='footer__flex-column-container'>
        <p className='footer__title'> </p>
        <p className='footer__subtitle'>    </p>
      </div>
      <div className='footer__flex-column-container'>
        <Link className='footer__link' to='/map'> </Link>
        <Link className='footer__link' to='/news'></Link>
        <Link className='footer__link' to='/papers'> </Link>
        <Link className='footer__link' to='/about-us'> </Link>
        <Link className='footer__link' to='/faq'>FAQ</Link>
      </div>
      <div className='footer__icons'>
        <Link className='footer__icon-link' to=''>
          <img className='footer__icon' src={classmatesIcon} alt='OK.ru' />
        </Link>
        <Link className='footer__icon-link' to=''>
          <img className='footer__icon' src={vkIcon} alt='' />
        </Link>
        <Link className='footer__icon-link' to=''>
          <img className='footer__icon' src={telegramIcon} alt='' />
        </Link>
      </div>
    </footer>
  );
}

export default Footer;
