import React from 'react';
import { useNavigate } from "react-router-dom"; //     
import { useInView } from 'react-intersection-observer'; //  ,      
import './SheltersOnMain.css';
import ShelterCard from '../ShelterCard/ShelterCard';
import shelterImage from '../../images/shelter-image.jpg';
import shelterLogo from '../../images/shelter-logo.jpg';


const SheltersOnMain = ({ loggedIn }) => {
  const [isActiveAnimation, setIsActiveAnimation] = React.useState(false); //  ?
  const [isOpenPopup, setIsOpenPopup] = React.useState(false); //      ?

  const navigate = useNavigate();

  const { ref, inView } = useInView({
    threshold: 1, //   ,      100%
  });

  const handleAddShelterButton = () => {
    loggedIn ? navigate('/profile') : setIsOpenPopup(true);
  }

  React.useEffect(() => {
    if (inView) { // ,     1 
      setIsActiveAnimation(true); //  
    };
  }, [inView])

  return (
    <section className='shelters-on-main' onClick={() => {isOpenPopup && setIsOpenPopup(false)}}>
      <h2 className='section-title'>,    </h2>
      <ul className='shelters-on-main__shelters-container'>
        <li>
          <ShelterCard
            image={shelterImage}
            logo={shelterLogo}
            title={' '}
            address={' -, 10, , '}
            workingHours={'10:00 - 18:00'}
            linkID={'/shelters/1'} />
        </li>
        <li>
          <ShelterCard
            image={shelterImage}
            logo={shelterLogo}
            title={' '}
            address={' -, 10, , '}
            workingHours={'10:00 - 18:00'}
            linkID={'/shelters/1'} />
        </li>
        <li>
          <ShelterCard
            image={shelterImage}
            logo={shelterLogo}
            title={' '}
            address={' -, 10, , '}
            workingHours={'10:00 - 18:00'}
            linkID={'/shelters/1'} />
        </li>
      </ul>
      <button className='button margin-left_auto' type='button' onClick={() => navigate('/shelters')}>  </button>
      <div className={`shelters-on-main__text-container ${isActiveAnimation ? 'shelters-on-main__text-container_animation' : ''}`} ref={ref}>
        <h3 className='shelters-on-main__text-title'>  ?</h3>
        <p className='shelters-on-main__text-subtitle'>        !</p>
        <button className={`button margin_column-center ${!loggedIn && 'button_disabled'}`} type='button' onClick={handleAddShelterButton}> </button>
        <div className={`shelters-on-main__popup ${isOpenPopup && 'shelters-on-main__popup_opened'}`}>
          <p className='shelters-on-main__popup-text'>       </p>
          <button className='button margin_column-center' type='button' onClick={() => navigate('/sign-up')}></button>
        </div>
      </div>
    </section>
  );
}

export default SheltersOnMain;
