import React from 'react';
import { useNavigate } from "react-router-dom"; //     
import './NotFoundPage.css';
import errorImage from '../../images/error-404.svg';

const NotFoundPage = () => {
  const navigate = useNavigate();

  return (
    <main className='main not-found'>
      <img className='not-found__404' src={errorImage} alt=' 404' />
      <p className='not-found__text'>,      :(</p>
      <p className='not-found__text'>     </p>
      <button className='button margin_column-center' type='button' onClick={() => navigate(-1)}> </button>
    </main>
  );
}

export default NotFoundPage;
